.moviesContain{
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 25vw;
}

.moviesList{
  display: grid;
  grid-template-columns: repeat(5, 236px);
  row-gap: 40px;
  column-gap: 80px;
}

.title{
  align-self: flex-start;
  position: relative;
  font-size: 20px;
  font-weight: 700;
  line-height: 30px;
  letter-spacing: 0%;
  text-align: left;
  text-transform: uppercase;
  margin-bottom: 40px;
}

.title::after{
  top: 40px;
  content: '';
  position: absolute;
  display: block;
  background: rgb(193, 39, 45);
  width: 30px;
  height: 4px;
}

.loader {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 48px;
  height: 48px;
  border: 5px solid #FFF;
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
  }

  @keyframes rotation {
  0% {
      transform: rotate(0deg);
  }
  100% {
      transform: rotate(360deg);
  }
  } 